import React from 'react';

export  const InputTodo = ({ name, onChange, onAdd }) => (
    <div>
      <input type="text" value={name} onChange={onChange} /> &nbsp;
      <button type="button" onClick={onAdd}>
        Add
      </button>
    </div>
  );

 export  const TodoList = ({todos}) => (
    <ul>
      {todos.map((todo) => (
         <li key={todo.id}>{todo.text}</li>
      ))}
    </ul>
  );

   