import React, {useState, useReducer, useRef} from 'react';

import {Header} from '../components/PageComp'


export const ButtonClick = () =>{

    const [count, setCount] = useState(0)
    return(
        <div>
       <h3> Button Click with useState </h3>
        <p> Tracking Button Click [ refer to: ButtonClick of UIComp]</p><br/>
      
        <button onClick={()=> setCount(count+1)}>
                  click Me
        </button> &nbsp;&nbsp;

         <p>You clicked {count} Times. </p>
         <hr />
        </div>
    );
}

export const ToggleCheckBoxUseState= () =>{
    const [checked, setChecked] = useState(false);

    return(
        <>
        <h3> Simple check box with useState </h3>
        <p>refer to [ToggleCheckBoxUseState of UIComp]</p><br/>
        <input 
         onChange = { ()=> setChecked(!checked)}
         type="checkbox" 
         value={checked}/>
       
         <p>The package is { checked ? "Deliver" : "Not Deliver"} </p><br/>
{/*         
        <Link to="/checkboxreducer">Checkbox with useReducer</Link><br/>
        <Link to="/dispatchActionrRducer">Dispatching actions with useReducer</Link> */}
        <hr/>
        </>
    );

    }

export const ToggleCheckBoxUseReducer= () =>{
        const [checked, setChecked] = useReducer( (checked) =>!checked, false)
      return(
           <>
           <h3> [Simple check box with useReducer]</h3>
             <p>refer to [ToggleCheckBoxUseReducer of UIComp]</p><br/>
          <input type="checkbox" value={checked} onChange={setChecked} />

            <p>The Reducer Package is {checked? "Delivered" : " Not Delivered" }</p><br/>
            <hr/>
         
            </>
       )
    
}
const initialState={
    message: "Hello"
}

function reducer(state, action){


    switch(action.type){
        case "yell":
           return{ message: `yell yell!!  Previous State: ${state.message}`}  
        case "whisper":
            return { message: `execuse me `}
        default:
             return {message: "Hello"}   
    }
}

export function DispatchActionReducer(){
   
    const [state, dispatch ] = useReducer(reducer, initialState);
    return(
        <>
        <h3>[Dispatch Action with useReducer]</h3>
        <p>refer to [DispatchActionReducer of UIComp]</p><br/>
        <p>Initial Message: {state.message}</p><br/>
        <button onClick={()=>dispatch({type: 'yell'}) }>Yell</button>
        <button onClick={()=>dispatch({type: 'whisper'}) }>Whisper</button>
        <hr/>
        </>
    )
}


export function FormUseRef(){
    //use to catch value from the form
  
    const sound = useRef();
    const color = useRef();
  
    const submit =(e) =>{
      e.preventDefault();
      const soundVal = sound.current.value ;
      const colorVal = color.current.value;
  
      alert(`${soundVal} sound like ${colorVal}`)
      sound.current.value ="";
      color.current.value ="";
  
  
    };
     return(
       <>
       <Header title="Form useRef to hold data" refer="refer to FormUseRef"/>
        <form onSubmit={submit}>
          <input ref={sound} type="text" placeholder="Sound...."  required/>
          <input ref={color} type="color" />
          <button >Add</button>
        </form>
        <hr/>
       </>
     )
  }

export const FormUseState = () => {
    const [sound, setSound] = useState("");
    const [color, setColor] = useState("");

    const submit =(e) =>{
         e.preventDefault();

         console.log(`${sound} is ${color}`)
         setSound("");
         setColor("");
        };

    return (
         <>
         <Header title="Form with useState to hold data" refer="refer to FormUseState" />
         <form onSubmit={submit}>
            <input type="text" 
            value={sound}
             onChange={(e)=> setSound(e.target.value)}
             placeholder="Color expression"
             required
             />
            <input type="color"
             onChange={(e)=> setColor(e.target.value)}
             value={color } />
            <button>Add</button>
         </form>

         {/* <form onSubmit={submit} > 
             <input type="text" 
             value ={sound}
             placeholder="color expression"
             onChange={(e)=>setColor(e.target.value)}
             />
            <input 
             type="color" 
             value ={color}
             onChange={(e)=> setColor(e.target.value)}
             />
             <button>Add</button>
         </form> */}
         <hr/>
         </>
    );

  }



export const Form3 = () =>{
  

    const Name = useRef();
    const email = useRef();
    const isEnabled = email.length > 0 && Name.length > 0;
   
     const submit =(e) =>{


     e.preventDefault(); 
    var NameVal = Name.current.value;
    var emailVal =email.current.value;
    
   

    alert(`Thank you for submiting
           Name:  ${NameVal} and email: ${emailVal}`)
   
           Name.current.value = "";
           email.current.value ="";

    }

    return(
        
    <form onSubmit={submit}>
            <table>
                <tbody>
                    <tr>
                        <td className="wide-screen-label-cell">
                            <label>Name:</label>
                        </td>
                        <td className="mobile-screen-label-cell">
                            <input 
                            type="text" 
                            ref={Name} 
                            placeholder="Enter your name" 
                            />
                        </td> 
                    </tr>
                  <tr>
                        <td className="wide-screen-label-cell">
                            <label>Email: </label>
                        </td> 
                        <td className="mobile-screen-label-cell">
                           <input type="text" ref={email} placeholder="email" />
                         </td>
                   </tr>
                   <tr >
                        <td className="wide-screen-label-cell" >   
                            <button disabled={!isEnabled}>Submit</button>
                        </td>
                        <td></td>
                   </tr>  
                   </tbody>
            </table>
    </form>

    );


}



   