import React from 'react';
import { Link } from 'react-router-dom';



var  yogaimg ={
                 width: "100px",
                  height: "110px",
                  display: "block",
                    borderRadius:"4px",
                    marginBottom:"5px",
                    marginTop: "2px"
                    
                  }
var title ={
  fontSize: "14px",
  textDecoration: "none",
  color: "#000",
  
}
const YogaL1 = ({yogas}) => 
(   
  
    <>
 
   
       {yogas.map((yoga) => {
        return(
        < >     
                    <Link className="yoga-list-item" style={title} key={yoga.id} to={`/yoga1/${yoga.name}`} >
                     {yoga.name}
                        <img style={yogaimg} src= {process.env.PUBLIC_URL + '/images/yoga/' + yoga.img}  alt={yoga.name}/>
                    </Link>                              
         
                   
       </>            
            );
                }) }
    </>
     
)

export default YogaL1;