import React from "react";
import { useForm } from "react-hook-form";
import { Header } from './PageComp';

function FormHookComp() {
  var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
  const {register, errors, handleSubmit } = useForm();
  const contactSubmit = data => {
  console.log(data.name);

  };
  
  return (
    <>
    <Header title="Contact Me: " refer="refer to function components/FormHookComp" /><br/>
    <form name="contactform" onSubmit={handleSubmit (contactSubmit)}>
      <div className="col-md-6">
        <fieldset>
          <input 
          name="name" 
          type="text" 
          size="30" 
          placeholder="Name" 
          required  
          ref={ register({pattern: /^[A-Za-z-\s]+$/i })} />
          <span style={{color: "red"}}>{errors.name && "Name is required"}</span>
          <br />

          <input 
          name="email" 
          type="text" 
          size="30" 
          placeholder="Email" 
          required 
          ref={register({pattern: pattern})} 
          />
          <span style={{color: "red", marginLeft: "10px"}}>{errors.email && "Invalid email "} </span>
          <br />

          <input 
          name="phone" 
          type="text" 
          size="30" 
          placeholder="Phone" 
          required 
          ref={register({pattern: /^[0-9-\s]+$/})} />
          <span style={{color: "red", marginLeft: "10px"}}>{errors.phone && "Invalid Phone"} </span>
          <br />
          <input name="address" type="text" size="30" placeholder="Address" ref={register} />
          <br />

        </fieldset>
      </div>
      <div className="col-md-6">
        <fieldset>
          <textarea name="message" cols="40" rows="10" className="comments" placeholder="Message" ref={register} />
        </fieldset>
      </div>
      <div className="col-md-12">
        <fieldset>
          <button className="btn btn-lg pro" id="submit" value="Submit">
            Send Message
          </button>
        </fieldset>
      </div>
    </form>
    </>
  );
}
export default FormHookComp;