import React, {Component} from 'react'
import ReactPaginate from 'react-paginate';
import { Link } from 'react-router-dom';
import yogacontent from '../data/yoga-content';
import { Header } from '../components/PageComp';

  

var  yogaimg ={
        width: "190px",
         height: "210px",
         }

               
export default class YogaListPage extends Component {
        constructor(props) {
            super(props);
            this.state = {offset: 0,
                          perPage: 3,
                          currentPage: 0
            };
            this.handlePageClick = this.handlePageClick.bind(this);
        }
        receivedData() {
            
                    const yogas = yogacontent.slice(this.state.offset, this.state.offset + this.state.perPage)
                     
                    const postData = yogas.map(yoga => 
                    <div className="yoga-item-Container" key={yoga.id}>
                        <div className="col1">
                                <Link className="yoga-list-item" to={`/yoga/${yoga.name}`} >
                                <img style={yogaimg} src= {process.env.PUBLIC_URL + '/images/yoga/' + yoga.img}  alt={yoga.name}/>
                                </Link> 
                        </div> 
                        <div className="col2"> 
                        <Link className="yoga-list-item" to={`/yoga1/${yoga.name}`} >
                                <h3>{yoga.name} ({yoga.lang})</h3> 
                                <p>{yoga.Instruction[0].substring(0,210)} ......</p> 
                        </Link>
                        </div>
                    </div>)  
    
                    this.setState({
                        pageCount: Math.ceil(yogacontent.length / this.state.perPage),
                        postData
                    })
              
        }
                handlePageClick = (e) => {
                    const selectedPage = e.selected;
                    const offset = selectedPage * this.state.perPage;
            
                    this.setState({
                        currentPage: selectedPage,
                        offset: offset}, () => {
                        this.receivedData()
                    });
            
                };
    
                componentDidMount() {
                    this.receivedData()
                }
        render() {
            return (
                <div>
                    <Header 
                    title="Import data from local JSON file" 
                    refer="refer to: pages/YogaListPage" />
                   
                    <p>Templates: [YogaPage and YogaPage1 component] Click image or header to see difference</p>               
                     <h2>My Yoga List:</h2> 
                   {this.state.postData}
                    <ReactPaginate
                        previousLabel={"prev"}
                        nextLabel={"next"}
                        breakLabel={"..."}
                        breakClassName={"break-me"}
                        pageCount={this.state.pageCount}
                        marginPagesDisplayed={2}
                        pageRangeDisplayed={5}
                        onPageChange={this.handlePageClick}
                        containerClassName={"pagination"}
                        subContainerClassName={"pages pagination"}
                        activeClassName={"active"}/>
                </div>
    
            )
        }
    }