import React from 'react';
import { Link } from 'react-router-dom';



var  yogaimg ={
                 width: "190px",
                  height: "200px",
                  }

const YogaList = ({yogas}) => 
(
    <>
       {yogas.map((yoga,key) => {
        return(
        <div className="yoga-item-Container"   key={key}>  
               <div className="col1">
                    <Link className="yoga-list-item" to={`/yoga/${yoga.name}`} >
                        <img style={yogaimg} src= {process.env.PUBLIC_URL + '/images/yoga/' + yoga.img}  alt={yoga.name}/>
                    </Link> 
              </div>                      
            <div className="col2"> 
                   <h3>{yoga.name} ({yoga.lang})</h3> 
                    <p>{yoga.Instruction[0].substring(0,210)} ......</p>  
                   
                   <p>Date Posted: {yoga.date}</p>
           </div>  
                   
       </div>            
            );
                }) }
    </>
     
)

export default YogaList;