import React from 'react';
import yogaData from '../data/yoga-content.js';
import YogaL1 from '../components/YogaL1';
import { Header } from '../components/PageComp';
import DemoFormComp from '../components/DemoFormComp';


var  yogaimg ={
     width: "500px",
     height: "520px",
     borderRadius: "14px",
     marginTop: "10px"
     }

const YogaPage1 = ({match}) =>{
    
      const name = match.params.name;
     const yoga =  yogaData.find(yoga => yoga.name === name );
       //const cat = yoga.cat;   
      // console.log(cat);    
      const otherYogaPoses = yogaData.filter(yoga => yoga.name !== name );  

      // const relativeYogaPose = yogaData.filter(yoga => yoga.cat === cat);
       
     return (  
         <>
        <div className="article">
        
        <div className="col1">    
           <Header title="" refer="refer to YogaPage1 and include YogaL1 component" />
            <h2>{yoga.name} ({ yoga.lang})</h2>
            {/* <h4>[refer to YogaPage1 and include YogaL1 component] </h4> */}
            <img style={yogaimg} src= {process.env.PUBLIC_URL + '/images/yoga/' + yoga.img}  alt={yoga.name}/>
            
            <h3>Step by step</h3>
            {yoga.Instruction.map((paragraph, key) => (
                <p key={key}>{paragraph}</p>
            ))}
        </div>
        
            <div className="col2">
                <h4>Other Yoga list:</h4>
                    <YogaL1 yogas ={otherYogaPoses} /> 
            </div>
        </div>
    
       <DemoFormComp />

       </> 
    );

}

export default YogaPage1;