import React from "react";
import peopleContent from '../data/people-data';


/*const inventory = [
    {name: 'apples', quantity: 2},
    {name: 'bananas', quantity: 0},
    {name: 'cherries', quantity: 5}
  ]; */
  
 
var personCss ={ float:"left",
                 width: "300px",
                  height: "300px",
                  }

const PersonPage = ({ match }) => {
    const id = match.params.id;
    //const people = peopleContent.find(people => people.id === id);
    //const article = articleContent.find(article => article.name === name);
   // const result = inventory.find( inventory => inventory.name === name );
  const result = peopleContent.find( peoples => peoples.id === id );
   
  //const result = peoples.find(people => people.id === id);
    if (!result) return <h1>People does not exist!</h1>

    return (
        <div className="person">
        <h2>
          {result.name}, {result.title}
        </h2>

        
          <img
            className="size-medium alignright"
            src={process.env.PUBLIC_URL + '/' + result.img}
            alt={result.name}
           style={personCss}
            sizes="(max-width: 300px) 100vw, 300px"
          />
          <p>
          {result.bio}
        </p>
      </div>
    );
}

export default PersonPage;