import React, {useState} from 'react';
import { Header, Main, Footer } from '../components/PageComp'



const dishesLunch =[
    "Macaroni and Cheese",
    "Salmon",
    "Tofu with Vegetable",
    "Beef noodle"
  ]
  const dishesDinner =[
    "Scampy Shrimp",
    "Ceacer Salad",
    "Sea food noodle",
    "Wondon soup"
  ]
  
const Lunch = dishesLunch.map((dish, i) =>({id: i, title: dish}) );
   //console.log("Object: " ,dishLunch);
  
   const Dinner = dishesDinner.map((dish, i) =>({id: i, title: dish}) );
const PageLayoutPage = () =>{
    const [serveTime] = useState("Lunch");
    return(
    <>
    <Header title="Construct a SPA with component." refer="refert to components/Header" />
    <Main adjective="Amazing" serveTime={serveTime} dishes ={ serveTime === "Dinner"? Dinner : Lunch}/>
    <Footer />
    </>

    )
}
export default PageLayoutPage;
 