import React, {useState, useEffect } from 'react';
//import articleContent from '../data/article-content';
import ArticleList from '../components/ArticleList';
import UpvotesSection from '../components/UpvotesSection';
import CommentsList from '../components/CommentsList';
import AddCommentForm from '../components/AddCommentForm';
import NotFoundPage from './NotFoundPage';


const ArticlePage = ({ match }) => {
    const name = match.params.name;
   // const article = articleContent.find(article => article.name === name);
    
    const [article, setArticleInfo] = useState({ upvotes: 0, comment: [], content: [] });
    const [articles, setArticls] = useState([]);
    useEffect(() => {
        const fetchData = async () => {
            const results = await fetch('/api/articles')
            const result = await fetch(`/api/articles/${name}`);
            const listbody = await results.json();
            const body = await result.json();
           console.log("List Articles: " , listbody);
           setArticls(listbody);
            setArticleInfo(body[0] );
        }
        fetchData();
    }, [name]);
    
    

    if (!article) return <NotFoundPage />

    const otherArticle = articles.filter( article => article.name !== name );

    return (
        <>
        <h1>{article.title}</h1>
     
        <UpvotesSection articleName ={name} upvotes={article.upvotes} setArticleInfo={setArticleInfo} />
         {/* { console.log ("content: ", article.content[0] ) } */}
          {article.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
    
        <CommentsList  comments={article.comment}/>
        <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
        <h3>Other Articles:</h3>
        <ArticleList articles ={otherArticle}/>
        </>
    );
}

export default ArticlePage;