import React, {useState} from 'react';
import {InputTodo, TodoList  } from '../components/InputItem';

import { Header } from '../components/PageComp'
import { v4 as uuidv4 } from 'uuid';



const initialList =[ { id: 1, text:"How to add input to state"}, 
{ id: 2, text:"How to delete input to state"},
{ id: 3, text:"How to update input to state"},
{ id: 4, text:"useState to additem"},
]
 const InsertPage = () => {
    const [text, setText] = React.useState('');
    const[todoList, setTodoList] = useState(initialList)
  
              function handleChange(event) {
                  setText(event.target.value);
              }
              
              function handleAdd() {
                  const newList = todoList.concat({ text , id: uuidv4()});
                  setTodoList(newList);
                  setText("");
              
              }
  
      
      return(
          <div>          
              
               <Header 
               title="Add item to State with useState" 
               refer="refer to pages/InsertPage; components/InputTodo and TodoList"
               />
               <InputTodo  name={text} onChange={handleChange} onAdd={handleAdd}/> 
               <TodoList todos={todoList} /> 
          </div>
      )
  }

  export default InsertPage;