import React from 'react';
import { SlideshowZoomIn, 
         SlideshowZoomOut, 
         SlideshowIndicator, 
         SlideshowFade,
         DynamicStyleDragEffect} from '../components/SlideshowComp';
import ImageCarousel from '../imagecomponents/ImageCarousel';         
 

const SlideshowGalleryPage = () =>(
        <>
        <SlideshowZoomIn />
        <SlideshowZoomOut />
        <SlideshowIndicator />
        <hr/>
        <h3>Rendering Component based on the props Value in following 2 examples below</h3>
       
        <DynamicStyleDragEffect design="color" border="4px" />
        <DynamicStyleDragEffect design="image" border="10px" />
        <ImageCarousel />
        <SlideshowFade />
        </>
)
export default SlideshowGalleryPage;

