import React, {useState, useEffect} from 'react';
import { Header } from '../components/PageComp';

function FetchHookPage(){
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() =>{
    
    
    setLoading(true)
    fetch(`https://api.github.com/users/fang-wenlee`)
    //fetch(`https://api.github.com/users`)
    .then( (res) => res.json())
    .then( setData )
    .then(() => setLoading(false))
    .catch(setError);
   }, []);

  //console.log(data)
  if(loading) return <h1>Loading.....</h1>
  if (error) return <pre>{JSON.stringify(error, null, 2)}</pre>
  
   if( !data) return null;
 
   return (
    <div> 
      <Header title="Fetch github API" refer="refer to pages/FetchHookPage"/>
    <h2>{data.name}</h2>
    <img src={data.avatar_url} alt={data.name}/><br/>
    <p>Company: {data.company}</p>
    <p>Location:{data.location}</p><br/>

  </div>
)

}
export default FetchHookPage;