import React from 'react';
import { Header } from '../components/PageComp';


import { Widelink } from '../components/WidecardComps';

 
 const SlideshowSamplesPage = () => (
    <>
    <Header title="Slide Show Examples" refer="refer to pages/SlideshowSamplesPage" />
        <ul>
         <Widelink title="Zoom in Effect"  to="zoom-in-effect"/>  
         <Widelink title="Zoom Out Effect"  to="zoom-out-effect"/> 
         <Widelink title="Fade Effect"  to="fade-effect"/> 
         <Widelink title="Drag Effect"  to="drag-effect"/> 
        {/* <li><Link to="zoom-in-effect"> Zoom in Effect</Link></li>
         <li><Link to="zoom-Out-effect"> Zoom out Effect</Link></li>
        <li><Link to="fade-effect">Fade Effect </Link></li>
        <li><Link to="drag-effect"> Drag Effect </Link></li> */}
       </ul>
    </>
)

export default SlideshowSamplesPage;

