
import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import './App.css';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ReactPage from './pages/ReactPage';
import UIPage from './pages/UIPage';
import InsertPage from './pages/InsertPage';
import PageLayoutPage from './pages/PageLayoutPage'
import CustomFetchHookPage from './pages/CustomFetchHookPage';
import FetchHookPage from './pages/FetchHookPage';

import FormSamplesPage from './pages/FormSamplesPage';
import SlideshowSamplesPage from './pages/SlideshowSamplesPage';
import SlideshowGalleryPage from './pages/SlideshowGalleryPage';
import {SlideshowZoomIn, SlideshowZoomOut, SlideshowFade, DragEffect} from './components/SlideshowComp';

import ContactFormComp from './components/ContactFormComp'
import FormHookComp from './components/FormHookComp';
import DemoFormComp from './components/DemoFormComp';
import SignUpForm from './components/SignUpForm';

import ArticlePage from './pages/ArticlePage';
import ArticleListPage from './pages/ArticleListPage';

import YogaListPage from './pages/YogaListPage';
import YogaPage from './pages/YogaPage';
import YogaPage1 from './pages/YogaPage1';
import PeopleListPage from './pages/PeopleListPage';
import PersonPage from './pages/PersonPage';
import NavBar from './NavBar';
//import Navbar from './Navbar';
import NavFoot from './NavFoot';
import NotFoundPage from './pages/NotFoundPage';
//Swithch component make sure that only one of routes that we wrap in it is rendered at a time
//so it will only rener the first route that matches the URL
//and Not found page place in the last in the route
function App() {
  return (
    <Router>
        <div className="App">
          <NavBar />
            <div id="page-body">
              <Switch>
              <Route path="/" component={HomePage} exact/>
              <Route path="/about" component={AboutPage} />
              <Route path="/react-sample" component={ReactPage} />
              <Route path="/page-layout" component={PageLayoutPage} />
              <Route path="/ui-page" component={UIPage} />
              <Route path="/insert-item" component={InsertPage} />
              <Route path="/fetch-hook" component={FetchHookPage} />
              <Route path="/custom-fetch-hook" component={CustomFetchHookPage} />
            
              <Route path="/form-samples" component={FormSamplesPage} />
              <Route path="/demo-form" component={DemoFormComp} /> 
              <Route path="/contact-form" component={ContactFormComp} /> 
              <Route path="/signup-form-hook" component={FormHookComp} />          
              <Route path="/signup-form" component={SignUpForm} /> 
           
           
             <Route path="/slide-show-gallery" component={SlideshowGalleryPage} />
             <Route path="/slide-show-samples" component={SlideshowSamplesPage} />
             <Route path="/zoom-in-effect" component={SlideshowZoomIn} /> 
             <Route path="/zoom-out-effect" component={SlideshowZoomOut} />
             <Route path="/fade-effect" component={SlideshowFade} />
             <Route path="/drag-effect" component={DragEffect} />
              <Route path="/article-list" component={ArticleListPage} />
              <Route path="/article/:name" component={ArticlePage} />
               <Route path="/yoga-list" component={YogaListPage} />  
                {/* can have different style for display; 
                /yoga/:name => list display at the bottome
                /yoga1/:name => listcomp display at the right */}
               <Route path="/yoga/:name" component={YogaPage} />   
               <Route path="/yoga1/:name" component={YogaPage1} />  


              <Route path="/people-list" component={PeopleListPage} />
              <Route path="/person/:id" component={PersonPage} />
              <Route component={NotFoundPage} />
              </Switch>
            </div>   
            <NavFoot />         
        </div>
    
    </Router>
  );
}

export default App;
