import React from 'react';
import {Link} from 'react-router-dom';

const PeopleList = ({peoples}) => 
(
<>
    { peoples.map(function (people,key){
        return(
             <Link className="people-list-item" key={key} to={`/person/${people.id}`}>
                 <h3>{people.title}</h3>
                 <p>{people.bio.substring(0,150)} ......</p>
             </Link>
        )

     }) }
</>
)

export default PeopleList; 