import React, { useState } from 'react';
import yogacontent from '../data/yoga-content';
import { Zoom, Fade, Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';
import {Header} from '../components/PageComp'
import './SlideshowComp.css'




export const SlideshowZoomIn = () => {
  const images = [
    'images/slideshow/slide_1.jpg',
    'images/slideshow/slide_2.jpg',
    'images/slideshow/slide_3.jpg',
    'images/slideshow/slide_4.jpg'
   ];

  const zoomInProperties = {
    indicators: true,
    scale: 1.4
  }
  return (
      <div style={{marginTop: "10px"}}>
        <Header title="ZoomIn Effect" refer="refer to components/SlideshowComp SlideshowZoomIn" />
      <Zoom {...zoomInProperties} style={{marginTop: "10px"}} >
        {images.map((each, index) => (
          <div key={index} style={{width: "100%"}}>
            <img style={{ objectFit: "cover", width: "100%" }} src={each} alt={each}/>
          </div>
        ))}
      </Zoom>
    </div>
  );
}

