export const SlideshowZoomOut_old = ({images}) => {
 
    const zoomOutProperties = {
        duration: 1000,
        indicators: true,
        scale: 0.4
    }

if (images){
  return (  
    <div style={{marginTop: "10px"}} >
  <Header title="ZoomOut Effect" refer="refer to components/SlideshowComp SlideshowZoomOut" />
    <Zoom {...zoomOutProperties}  >
      {images.map((image) => (
        <div key={image.id} style={{width: "60%", textAlign:"center", padding:"100px"}}>
         
          <img 
          style={{ objectFit: "cover", width: "70%"}} 
          src={image.avatar_url} 
          alt={image.login}
          />
        </div>
      ))}
    </Zoom>
  </div>    
    )}else{
  
      const imagesArr = [
        'images/slideshow/slide_2.jpg',
        'images/slideshow/slide_3.jpg',
        'images/slideshow/slide_4.jpg'
      ];     
      
   
    return (
      <div style={{marginTop: "10px"}} >
    <Header title="ZoomOut Effect" refer="refer to components/SlideshowComp SlideshowZoomOut" />
    <Zoom {...zoomOutProperties}  >
      {imagesArr.map((image, index) => (
        <div key={index} style={{width: "100%", textAlign:"center"}}>
           
          <img 
          style={{ objectFit: "cover", width: "100%"}} 
          src={image} 
          alt={image}
          />
        </div>
      ))}
    </Zoom>
  </div> 
    )
  }}
