const ZoomOut = ({images}) =>{
  const zoomOutProperties = {
    duration: 2000,
    indicators: true,
    scale: 0.4
   }
   return (  
    <div style={{marginTop: "10px"}} >
  <Header title="ZoomOut Effect" refer="refer to components/SlideshowComp SlideshowZoomOut" />
    <Zoom {...zoomOutProperties}  >
      {images.map((image) => (
        <div key={image.id} style={{ textAlign:"center"}}>
            <h2 >{image.login}</h2>
          <img 
          style={{ objectFit: "cover"} } 
          src={image.avatar_url} 
          alt={image.login}
          />
        </div>
      ))}
    </Zoom>
  </div>    
    )

}


export const SlideshowZoomOut = ({images}) => {
 
if (images){
return (  <ZoomOut images ={images} size="50%" />  )
}else{

    const imagesArr = [
      'images/slideshow/slide_2.jpg',
      'images/slideshow/slide_3.jpg',
      'images/slideshow/slide_4.jpg'
    ];     
//const images = imagesArr.map((image, i) =>({id: i, title: image}) );
  const imgs = imagesArr.map( (img, i)  => ({ id:i , avatar_url: img }))
  return (
    <ZoomOut images ={imgs} />
  )
}}




