export const SlideshowFade = () => {
   
    // const fadeImages = [
    //    'images/slideshow/slide_1.jpg',
    //    'images/slideshow/slide_2.jpg',
    //    'images/slideshow/slide_3.jpg',
    //    'images/slideshow/slide_4.jpg'
    //   ];
  
       //const images = imagesArr.map((image, i) =>({id: i, title: image}) );
       const images = yogacontent.map((item, i) =>({ id: i, img_url: "/images/yoga/"+item.img}) )

  
   return (
     <div>
        <Header title="Fade Effect" refer="refer to components/SlideshowComp  SlideshowFade" />
       <div className="slide-container">
         <Fade duration={1000} style={{marginTop: "10px"}}>
        
            {images.map((image) => (
        <div key={image.id} style={{width: "100%"}}>
          <img style={{  width: "500px" }} src={process.env.PUBLIC_URL + image.img_url} alt={image.img_url}/>
        </div>
      ))}
           
           
         </Fade>
       </div>
     </div>
   );
 };
//===================================================
export const SlideshowIndicator = () => {
  const images = [
       'images/slideshow/slide_1.jpg',
       'images/slideshow/slide_2.jpg',
       'images/slideshow/slide_3.jpg',
       'images/slideshow/slide_4.jpg'
  ];

  const zoomOutProperties = {
  
    scale: 0.4,
    indicators: i => (<div className="indicator">{i + 1}</div>)
  }
  return (
    <div>
      <Header title="Custom Indicators" refer="refer to components/SlideshowComp  SlideshowIndicator" />
      <Zoom {...zoomOutProperties}>
        { images.map((each, index) => <img key={index} style={{width: "100%"}} src={each}  alt={each}/>) }
      </Zoom>
    </div>
  )
}



