export const DragEffectOld = () => {
    const [autoplay, setAutoplay] = useState(true);
  
    const style = {
      textAlign: "center",
      background: "teal",
      padding: "200px 0",
      fontSize: "40px"
    }
  
    return (
        <div style={{marginTop: "50px"}}>
             <Header title="DragEffect" refer="refer to components/SlideshowComp  DragEffect" />
        <div>
          <Slide autoplay={autoplay} duration={1000} style={{marginTop:"10px"}}>
            <div style={{...style, background: 'teal'}}>
              First Slide
            </div>
            <div style={{...style, background: '#6c6ce3'}}>
              Second Slide
            </div>
            <div style={{...style, background: '#8fe78f'}}>
              Third Slide
            </div>
            <div style={{...style, background: '#ccc'}}>
            Fourth Slide
          </div>
          </Slide>
        </div>
  
        <div className="autoplay-buttons">
          Autplay is {autoplay ? 'on' : 'off'}
        </div>
        <div className="autoplay-buttons">
          <button type="button" onClick={() => setAutoplay(false)}>Pause</button>
          <button type="button" onClick={() => setAutoplay(true)}>Play</button>
        </div>
      </div>
    );
};



function ColorBackground({images}){

     const [autoplay, setAutoplay] = useState(true);
     const style = {
      textAlign: "center",
      background: "teal",
      padding: "100px 0",
      fontSize: "50px",
      width: "100%", 

      color: "white"
    }

    return (  
      <div style={{marginTop: "50px"}}>
        <Header title="DragEffect and passing color props" refer="refer to components/SlideshowComp  DragEffect" />
    <div>
     <Slide autoplay={autoplay} duration={1000} style={{marginTop:"10px"}}>
       
         { images.map( each =>        
          <div key={each.id} style={{ ...style, background: each.color }}  >
            {each.id} 
          </div> )}
     </Slide>
   </div>

   <div className="autoplay-buttons">
     Autplay is {autoplay ? 'on' : 'off'}
   </div>
   <div className="autoplay-buttons">
     <button type="button" onClick={() => setAutoplay(false)}>Pause</button>
     <button type="button" onClick={() => setAutoplay(true)}>Play</button>
   </div>
 </div>
); 
}
 
function ImageBackground ({images}){

    const [autoplay, setAutoplay] = useState(true);
    const style = {
     textAlign: "center",
     background: "teal",
     padding: "100px 0",
     fontSize: "50px",
     width: "100%",
     color: "white"
   }

   return (  
     <div style={{marginTop: "50px"}}>
       <Header title="DragEffect and passing Image props" refer="refer to components/SlideshowComp  DragEffect" />
   <div>
    <Slide autoplay={autoplay} duration={1000} style={{marginTop:"10px"}}> 
       { images.map( each =>          
         <div key={each.id} style={{ ...style,  background: `url(${each.name} )`, backgroundRepeat: "no-repeat" }}  >
           {each.id} 
         </div> )}
    </Slide>
  </div>
  <div className="autoplay-buttons">
    Autplay is {autoplay ? 'on' : 'off'}
  </div>
  <div className="autoplay-buttons">
    <button type="button" onClick={() => setAutoplay(false)}>Pause</button>
    <button type="button" onClick={() => setAutoplay(true)}>Play</button>
  </div>
</div>
); 
 }
  
