import React from 'react';
import { Header } from './PageComp';  

function SuccessSubmit(props){
 var successFormDisplay = props.successFormDisplay;
  return(
    <div  style={{ display: successFormDisplay, textAlign: "center", color: "red"}}>
      <h2>Success!</h2>
      <h3>You have Successful Submitted a form.</h3>
    </div>
  )
}


class DemoFormComp extends React.Component {
    constructor() {
    super();
    this.state = {
      input: {},
      errors: {},
      isValidate : false
    };
     
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
     
  handleChange(event) {
    let input = this.state.input;
    input[event.target.name] = event.target.value;
  
    this.setState({
      input
    });
  }
    
  handleSubmit(event) {
    event.preventDefault();
  
    if(this.validate()){
        console.log(this.state);
  
        let input = {};
        input["name"] = "";
        input["email"] = "";
        input["comment"] = "";
        this.setState({input:input});
       this.setState({isValidate: true})
        alert('Demo Form is submited');
    }
  }
  
  validate(){
      let input = this.state.input;
      let errors = {};
      let isValid = true;
  
      if (!input["name"]) {
        isValid = false;
        errors["name"] = "Please enter your name.";
      }

      if(typeof input["name"] !== "undefined"){
        if(!input["name"].match(/^[a-zA-Z-\s]+$/i)){
          isValid = false;
           errors["name"] = "Invalid name input";
        }        
     }
  //=================================================
      if (!input["email"]) {
        isValid = false;
        errors["email"] = "Please enter your email Address.";
      }
  
      if (typeof input["email"] !== "undefined") {
          
        var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
        if (!pattern.test(input["email"])) {
          isValid = false;
          errors["email"] = "Please enter valid email address.";
        }
      }
  //========================================
      if (!input["comment"]) {
        isValid = false;
        errors["comment"] = "Please enter your comment.";
      }
  
      this.setState({
        errors: errors
      });
  
      return isValid;
  }
     
  render() {
    const { isValidate } = this.state;
    const successFormDisplay = isValidate ? "block" : "none";
    const inputFormDisplay  =  !isValidate ? "block" : "none";
    return (
<>
       <SuccessSubmit successFormDisplay={successFormDisplay}/>
      <div style={{display: inputFormDisplay}}>
        <Header title="React Form Validation Example" refer="refer to class component/DemoFormComp" />
        {/* <h1>React Form Validation Example</h1> */}
        <br/>
        <form onSubmit={this.handleSubmit}>
  
          <div className="form-group">
            <label style={{marginRight: "70px"}}>Name:</label>&nbsp;
            <input 
              type="text" 
              name="name" 
              value={this.state.input.name}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Enter name" 
              id="name" />
  
              <div className="text-danger" style={{color: "red"}}>{this.state.errors.name}</div>
          </div>
  
          <div className="form-group">
            <label> Email Address: </label> &nbsp;
            <input 
              type="text" 
              name="email" 
              value={this.state.input.email}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Enter email" 
              id="email" />
   
              <div className="text-danger" style={{color: "red"}}>{this.state.errors.email}</div>
          </div>
  
          <div className="form-group">
            <label >Comment:</label>
            <textarea 
              name="comment"
              value={this.state.input.comment} 
              onChange={this.handleChange}
              placeholder="Enter comment"
              className="form-control" 
              style={{marginTop: "10px"}}
              />
  
              <div className="text-danger" style={{color: "red"}}>{this.state.errors.comment}</div>
          </div>
             
          <input type="submit" value="Submit" className="btn btn-success" />
        </form>
      </div>

      </>
    );
  }
}
  
export default DemoFormComp;