import React from 'react';
import { useFetch } from '../customHooks/useFetch';
//import ProfileItem from '../components/ProfileItem';
import { Header } from '../components/PageComp';
import {SlideshowZoomOut} from '../components/SlideshowComp'
import './CustomFetchHookPage.css';

const CustomFetchHookPage =() =>{
    const { loading, data, error} = useFetch(`https://api.github.com/users`)
    if(loading) return <h1>Loading ......</h1>;
  
    if(error) return <pre>{JSON.stringify(error, null)}</pre>
    //console.log(data);
     
       if(data){
           return( 
                    <>
                    {/* <h2>Custom Fetch [refer to pages/CustomFetchHookPage]</h2> */}
                    <Header title ="Create a custom hook: [useFetch]; Call github API; " 
                            refer ="refer to pages/CustomFetchHookPage"/>
                   
                    <SlideshowZoomOut images={data} />
            
                    {/* {data.map( (user) =>(
                        <ProfileItem people={user} key={user.id} />
                    ))} */}

                    
             </>
            );
      }

    }

export default CustomFetchHookPage;